<script>
export default {
  name: 'Login',
  methods: {
    // Log the user in
    login() {
      this.$auth.loginWithRedirect();
    },
    // Log the user out
    logout() {
      this.$auth.logout({
        returnTo: window.location.origin,
      });
    },
  },
};
</script>

<template>
  <div class="columns background has-no-margin">
    <div class="column is-half is-offset-one-quarter">
      <div class="box has-mt-2">
        <h1 class="title is-size-1 has-text-centered">Pihlen</h1>
        <p>Po pihlen budete moct pst pbhy a sdlet je se svmi pteli.</p>
        <p>V souasn dob podporujeme pihlen pouze prostednictvm tu Google. K&nbsp;autentizaci
          vyuvme slubu <a href="https://auth0.com">auth0</a>. Po kliknut na tlatko budete
          pesmrovni na strnky sluby.</p>
        <p>Pro pihlen vyuvme jen nezbytn minimum daj o vaem tu Google.</p>
        <div class="has-mt-1" v-if="!$auth.loading">
          <b-button v-if="!$auth.isAuthenticated" @click="login" type="is-primary" expanded>Pejt na pihlen</b-button>
          <b-button v-if="$auth.isAuthenticated" @click="logout">Log out</b-button>
        </div>
      </div>
    </div>
  </div>
</template>

